/* The animation code */
@keyframes drop-1-anim {
  from {transform: translateY(-100%);}
  to {transform: initial;}
}
@keyframes drop-2-anim {
  from {transform: translateY(-200%);}
  to {transform: initial;}
}
@keyframes drop-3-anim {
  from {transform: translateY(-300%);}
  to {transform: initial;}
}
@keyframes drop-4-anim {
  from {transform: translateY(-400%);}
  to {transform: initial;}
}
@keyframes wiggle-anim {
  0% {transform: rotate(0deg);}
  25% {transform: rotate(-10deg);}
  75% {transform: rotate(10deg);}
  100% {transform: rotate(0deg);}
}

/* The element to apply the animation to */
.drop-1 {
  animation-name: drop-1-anim;
  animation-duration: 1s;
}
.drop-2 {
  animation-name: drop-2-anim;
  animation-duration: 2s;
}
.drop-3 {
  animation-name: drop-3-anim;
  animation-duration: 3s;
}
.drop-4 {
  animation-name: drop-4-anim;
  animation-duration: 4s;
}

