.board-col .row {
  margin-bottom: 1rem;
}

.board-col .row > * {
  margin-left: auto;
  margin-right: auto;
}

.untouchable {
  /* background: #80000088; */
  user-select: none;
  touch-action: none;
}

.board {
  float: left;
  border: 2px solid black;
  padding: 1rem;
  border-radius: 1rem;
  line-height: 1;
}

.over .board, .over .board-footer, .over .play-area  {
  opacity: 0.5;
  pointer-events: none;
}

.overlay {
  position: absolute;
  z-index: 10;
  width: 100%;
  margin-top: 10rem;
  text-align: center;
}

.board-footer {
  text-align: center;
}

.play-area {
  float: left;
  /* background: #efefef; */
  /* border: 2px solid black; */
  /* padding: 0rem; */
  /* border-radius: 1rem; */
  line-height: 1;
}

.play-area > .row {
  margin-bottom: -0.6em;
}

.container {
  width: 100%;
  clear: both;
  float: left;
  display: flex;
}

.flex-grow {
  float: left;
  flex-grow: 1;
}

.play-button {
  float: left;
  align-self: flex-end;
}

.tile {
  font-family: 'HelveticaNeue-Bold';
  color: #461F13;
  float: left;
  width: 6rem;
  height: 6rem;
  transform-origin: 50% 50%;
  padding: 0.1rem;
}

.play-area .tile {
  width: 4rem;
  height: 4rem;
  font-size: 3em;
}

.board .tile:nth-child(4n+1) {
  clear: both;
}

.start-row:last-child {
  clear: initial;
}

.tile.space {
  visibility: hidden;
}

.board .tile .field {
  height: 100%;
}

