.scooched .points {
  margin: 0.5rem;
  background: repeating-linear-gradient( 
  transparent,
  transparent 4px,
  #666 4px,
  #666 5px);
}

span.field {
  border-radius: 0.1rem;
  padding-left: 0.1rem;
  padding-right: 0.1rem;
}

.tile .letter {
  text-align: center;
  height: 70%;
  font-size: 4rem;
  font-weight: bold;
}

.tile .points {
  font-family: 'HelveticaNeue-Medium';
  text-align: right;
  vertical-align: bottom;
  font-weight: bold;
  padding-right: 0.5rem;
  padding-top: 0.4rem;
  margin-bottom: -0.6rem;
}

.play-area .tile .field {
  border-radius: 0.4rem;
}

.play-area .tile .letter {
  font-size: 3rem;
}

.play-area .tile .points {
  font-size: 1rem;
}

.play-area .tile .bonus {
  position: relative;
  background-color: #c7b3ff;
  border: 1px solid #800000;
  border-radius: 1rem;
  font-size: 0.8rem;
  z-index: 2;
  text-align: center;
  width: 1.2rem;
  height: 1.2rem;
  float: right;
  top: -0.9rem;
  right: -0.3rem;
}

.tile.dragging .points {
  color: transparent;
}

