.selectable {
  cursor: pointer;
}

.unselectable.swappable {
  cursor: grab;
}

.wiggle .tile.swappable {
  animation: wiggle-anim 0.3s infinite;
}

.pre-dragging {
  cursor: grab;
}

.dragging {
  cursor: grab;
}

.unselectable {
  opacity: 0.5;
  cursor: default;
}

.selected, .selected div {
  color: orchid;
  opacity: 0.5;
  /* "alias" looks like "undo" (which is what clicking on an already-selected tile does) */
  cursor: alias;
}

.tile.selected .field, .field.selected  {
  background: transparent;
}

.placeholder {
  visibility: hidden;
}

.players {
  margin: 0.2rem;
  border: 1px solid black;
  background: #efefef;
}

.player .header {
  font-size: 2em;
}

.player.current .header .name * {
  font-weight: bold;
}

.player .header .score {
  margin-left: 0.2rem;
}

.moves .badge {
  text-transform: uppercase;
  font-family: 'Jura', sans-serif;
  opacity: 0.8;
}

