.moves .tile {
  padding: 0 !important;
  margin: 0.05em;
  margin-bottom: .3rem;
  width: 1.3rem;
  height: 1.3rem;
  font-size: 1em;
}

.moves .tile .field {
  padding: 0 !important;
  border-radius: 0.25rem;
  border-color: #00000040;
  background: none;
}

.moves .tile .letter {
  padding: 0 !important;
  position: relative;
  top: -0.4rem;
  height: 1.3rem;
  font-size: 1rem;
}

.moves .tile .points {
  padding: 0 !important;
  position: relative;
  top: -0.7rem;
  height: 0;
  /* margin-top: -0.6rem; */
  font-size: 0.1rem;
}

.moves .bonus {
  float: left;
  background-color: #c7b3ff40;
  border: 1px solid #80000040;
  border-radius: 1rem;
  font-size: 0.8rem;
  text-align: center;
  margin: 0.1rem;
  padding-left: 0.2rem;
  padding-right: 0.2rem;
}

.score {
  text-align: right;
  padding: 0;
}

.turn-indicator {
    display: inline-block;
    margin-top: 0.5rem;
    border-top: 1rem solid transparent;
    border-right: 1rem solid;
    border-bottom: 1rem solid transparent;
}

.player .name input {
  width: 100%;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid lightgray;
  background: transparent;
}

h1 input:hover {
  background: white;
  border-bottom: 1px solid gray;
}

.tile-definition {
  display: inline-block;
  width: 25%;
}


@media (max-width: 400px) {
  .board {
    padding: 0;
    margin: 0;
    border: none;
  }

  .board .tile {
    width: 4rem;
    height: 4rem;
  }  

  .board .tile .letter {
    margin-top: -0.2rem;
    font-size: 3rem;
  }
  
  .board .tile .points {
    margin-top: 0.2rem;
    font-size: 0.8rem;
  }
  
  .play-area .tile {
    width: 2.5rem;
    height: 2.5rem;
  }
    
  .play-area .tile .field {
    border-radius: 0.4rem;
  }
  
  .play-area .tile .letter {
    font-size: 2rem;
  }
  
  .play-area .tile .points {
    font-size: 0.75rem;
  }  
}

/* 576 is boostrap's sm */
@media (min-width: 576px) {
  .sm-left {
    text-align: left;
  }

  .sm-right {
    text-align: right;
  }
}

@keyframes pulse {
  0% {
    text-shadow: 0 0 0 gold;
  }
  50% {
    text-shadow: 0 0 2px gold;
  }
  100% {
    text-shadow: 0 0 0 gold;
  }
}

/* .goal {
} */

.goal.possible {
  animation-name: pulse;
  animation-duration: 4s;
  animation-direction: normal;
  animation-iteration-count: infinite;
}

.goal.qualifies {
  font-weight: bolder;
}


@keyframes announcement-anim {
  0% {
    transform: translateX(-50%);
    opacity: 0;
  }
  25% {
    transform: translateX(-1%);
    opacity: 1;
  }
  75% {
    transform: translateX(1%);
    opacity: 1;
  }
  80% {
    transform: translateX(50%);
    opacity: 0;
  }
  100% {
    transform: translateX(100%);
    opacity: 0;
  }
}

.announcement {
  animation-name: announcement-anim;
  animation-duration: 3s;
  width: 100%;
  background-image: linear-gradient(to right, transparent, gold, transparent);
  font-weight: bold;
}

.announcement .title {
  font-family: 'Jura', sans-serif;
  font-size: 3rem;
  text-transform: uppercase;
}

.announcement .message {
  font-family: 'Jura', sans-serif;
  font-size: 1rem;
}



@keyframes bulge-anim {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.can-play {
  animation: bulge-anim 300ms linear;
}
